.techstack {
  padding: 0 4rem;
  display: flex;
  height: 80vh;
  margin-top: 5rem;
}

.t-left {
  flex: 1;
  display: flex;
  flex-direction: column;
  transform-style: preserve-3d;
  text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.3);
  backface-visibility: hidden;
  scale: 1;
  width: 50%;
}

.t-left> :nth-child(1) {
  font-size: clamp(2rem, 3.5vw + 1.5rem, 4rem);
  color: var(--black);
  font-weight: bold;
}

.t-left> :nth-child(2) {
  font-size: clamp(3rem, 4vw + 2rem, 5rem);
  color: var(--purplelight);
  font-weight: bold;
}

.t-left> :nth-child(3) {
  width: 30rem;
  color: var(--gray);
  font-weight: 400;
  font-size: 15px;
  font-family: 'Verdana', Geneva, Tahoma, sans-serif;
  margin-top: 3rem;
  text-align: justify;
  text-shadow: none;
  line-height: 1.5;
}

.t-right {
  position: relative;
  width: 45%;
  top: 5rem;
}

.t-mainCircle {
  position: relative;
  left: 7rem;
  width: 18rem;
  height: 18rem;
  border-radius: 100%;
  box-shadow: var(--smboxShadow);
  top: 2rem;
  background-color: white;
}

.t-mainCircle>* {
  position: absolute;
}

.t-mainCircle> :nth-child(1) {
  top: -3rem;
  left: 6rem;
}

.t-mainCircle> :nth-child(2) {
  left: -3rem;
  top: 5rem;
}

.t-mainCircle> :nth-child(3) {
  left: 6rem;
  top: 5rem;
}

.t-mainCircle> :nth-child(4) {
  left: 15rem;
  top: 5rem;
}

.t-mainCircle> :nth-child(5) {
  top: 13rem;
  left: 6rem;
}

.t-secCircle {
  width: 6rem;
  height: 6rem;
  position: absolute;
  border-radius: 100%;
  border: 5px solid #ebebeb;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: var(--smboxShadow);
  background: white;
}

.t-secCircle>img {
  transform: scale(0.04);
}

#t-secCircle2>img {
  transform: scale(0.025);
}

#t-secCircle3>img {
  transform: scale(0.06);
}

#t-secCircle1>img {
  transform: scale(0.03);
}

.t-backCircle {
  position: absolute;
  height: 13rem;
  width: 13rem;
  border-radius: 100%;
  z-index: -1;
}

.blueCircle {
  background-color: var(--purple);
  left: 15rem;
  top: 1rem;
}

.yellowCircle {
  background-color: #916aac;
  left: 15rem;
  top: 8rem;
}


@media screen and (min-width: 1024px) and (max-width: 1224px) {
  .t-right {
    position: relative;
    /* left: -10rem; */
    top: 5rem;
    transform: scale(0.95);
  }
}



@media screen and (min-width: 768px) and (max-width: 1024px) {
  .techstack {
    flex-direction: column;
    align-items: center;
    padding: 0 2rem;
    width: 95%;
    height: auto;
    margin-top: 0;
  }

  .t-right {
    transform: scale(0.90);
    width: auto;
    top: 0;
    height: auto;
    display: flex;
    justify-items: center;
    align-items: center;
    left: -5rem;
  }

  .t-backCircle {
    transform: scale(0.85);
  }

  .t-left {
    flex-direction: column;
    align-items: center;
    /* scale: 0.95; */
    width: 100%;
    margin-bottom: 3rem;
  }

  .t-left> :nth-child(3) {
    width: 100%;
    max-width: 90%;
  }
}

@media screen and (max-width: 768px) {
  .techstack {
    flex-direction: column;
    align-items: center;
    height: auto;
    padding: 0 1rem;
    gap: 3rem;
  }

  .t-left {
    flex-direction: column;
    align-items: center;
    /* scale: 0.95; */
    width: 100%;
    /* gap: 1rem; */
  }

  /* .t-left > :nth-child(1) {
    font-size: 2.5rem;
    text-align: center;
  }

  .t-left > :nth-child(2) {
    font-size: 3.2rem;
    text-align: center;
  } */

  .t-left> :nth-child(3) {
    font-size: 15px;
    width: 100%;
    max-width: 90%;
    margin: 0 auto;
    text-align: justify;
  }

  .t-right {
    position: relative;
    left: -10rem;
    top: 3rem;
    /* transform: scale(0.8); */
  }
}


.techstack-para {
  margin-top: 1.2rem;
  font-size: 1.1rem;
}


@media screen and (max-width: 480px) {
  .techstack {
    flex-direction: column;
    height: 45rem;
    padding: 0;
    justify-content: center;
    align-items: center;
    padding: 0 1.5rem;
  }

  .t-right {
    /* display: flex; */
    align-items: center;
    justify-content: center;
    transform: scale(0.80);
    left: -9rem;
  }



  .t-left> :nth-child(3) {
    width: 100%;
    /* max-width: 95%; */
    margin: 1rem auto !important;
    text-align: justify;
  }

